.item {
    margin: 16px 0 16px 0;
    border-bottom: 1px solid #E5E5E5;
}

.item:last-of-type {
    border-bottom: none;
}

.users {
    display: flex;
    flex-direction: column;
    gap: 32px;
}

.buttonsContainer {
    display: flex;
    justify-content: right;
    gap: 12px;
}

.container {
    display: flex;
    flex-direction: column;
    max-width: 606px;
}

.thead {
    display: flex;
    align-items: center;
    margin-top: 18px;
    min-height: 36px;
    background-color: #E5E5E5;
    font-size: 16px;
    font-weight: 600;
}

.nameCol {
    padding-left: 17px;
    padding-right: 280px;
}

